<template>
  <!-- <v-card class="mx-auto"> -->
    <v-data-table
      :headers="headers"
      :items="inmuebles"
      :loading="isCargando"
      dense
      class="elevation-5"
    >
      <template v-slot:item.status="{item}">
        <v-chip :color="item.status ? 'red': 'green'" v-text="item.status ? item.error: 'Listo'" dark/>
      </template>
      <template v-slot:item.accion="{item}">
        <v-icon color="info" @click="habilitarEdicion(item)" small>mdi-pencil</v-icon>
        <v-icon color="red" @click="eliminarInmuebleAgregado(item)" small>mdi-delete</v-icon>
      </template>
      <template v-slot:top>
        <v-dialog v-model="dialog" width="500">
          <actualizar-inmueble />
        </v-dialog>
      </template>
    </v-data-table>
    <!-- <v-card-actions>
      <v-spacer />
      <v-btn color="success" @click="CrearInmuebles">Crear Inmuebles</v-btn>
    </v-card-actions>
  </v-card> -->
</template>

<script>
import ActualizarInmueble from '../components/Inmueble/FormularioCrearInmueblesManual.vue'
export default{
  components: { 'actualizar-inmueble': ActualizarInmueble },
  data: () => ({
    habilitar_edicion: false,
    headers: [   
      { value: 'status', text: 'Status', sortable: true },   
      { value: 'clave', text: 'Clave', sortable: false },
      { value: 'nombre', text: 'Inmueble',sortable: false },
      { value: 'estado.nombre', text: 'Estado',sortable: true },
      { value: 'municipio.nombre', text: 'Municipio', sortable: true },
      { value: 'localidad.nombre', text: 'Localidad', sortable: true },
      { value: 'direccion', text: 'Direccion', sortable: false },      
      { value: 'accion', text: '', sortable: false},
    ],
    dialog: false
  }),
  computed:{
    inmuebles(){ return this.$store.getters["MOD_inmuebles/getInmuebles"] },
    isCargando() { return this.$store.getters["MOD_inmuebles/getCargando"]; },
  },
  methods:{
    eliminarInmuebleAgregado(inmueble){
      this.$store.commit("MOD_inmuebles/eliminarInmueble", inmueble)
    },
    habilitarEdicion(index){
      this.$store.commit("MOD_inmuebles/setInmueble", index)
      index.estado.id ? this.obtenerMunicipiosDeEstado(index.estado) : []
      this.dialog = true
    },
    async CrearInmuebles(){
      const flag_crear_inmuebles = await this.$store.dispatch("MOD_inmuebles/AXN_CrearInmuebles", 
        this.$store.state.MOD_inmuebles.inmuebles
      )
      switch (flag_crear_inmuebles) {
          case 2:
            alert('Inmuebles creados con exito')
            break
          default:
            alert('Algo inesperado paso')
            break
        }
    },
    async obtenerMunicipiosDeEstado({ id }) {
      await this.$store.dispatch("MOD_ubicaciones/obtenerMunicipiosDeEstado", id);
    },
  }
}
</script>
