<template>
  <v-card class="mx-auto container">
    <v-row no-gutters>
      <v-col cols="12">
        <v-radio-group v-model="tipo" row>
          <v-radio value="cliente" label="Cliente" />
          <v-radio value="estado" label="Estado" />
          <v-radio value="fecha" label="Fecha" />
        </v-radio-group>
        <seleccionar-fecha
          v-if="tipo == 'fecha'"
          @eventFecha="inicializarTrabajos"
        />
        <v-select
          v-if="tipo == 'cliente'"
          v-model="cliente"
          :items="clientes"
          item-text="nombre"
          item-value="id"
          label="Cliente"
          clearable
          outlined dense
        />
        <v-select
          v-if="tipo == 'estado'"
          v-model="estado"
          label="Estado"
          item-text="nombre"
          :items="estados"
          item-value="id"
          clearable
          outlined dense
          no-data-text="No hay estados"
        />
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
import SeleccionarFechas from "../../components/Seleccion/Date.vue";
export default {
  data: () => ({
    tipo: null,
    cliente: null,
    estado: null,
  }),
  components: {
    "seleccionar-fecha": SeleccionarFechas,
  },
  computed: {
    clientes() { return this.$store.getters["MOD_clientes/getClientes"]; },
    estados() { return this.$store.getters["MOD_ubicaciones/getEstados"]; },
  },
  created() {
    const { cliente, estado, menorIgualFecha, mayorIigualFecha } = this;
    this.inicializarTrabajos({
      cliente: cliente,
      estado: estado,
      menorIgualFecha: menorIgualFecha,
      mayorIigualFecha: mayorIigualFecha,
    });
    // this.inicializarClientes();
  },
  methods: {
    async inicializarTrabajos({ cliente, estado, menorIgualFecha, mayorIigualFecha, }) {
      this.$store.commit("MOD_trabajos/cleanTrabajos")
      const { mensaje, error, estados } = await this.$store.dispatch("MOD_trabajos/obtenerTrabajosPendietes", {
        client: cliente,
        province: estado,
        menorIgualFecha: menorIgualFecha,
        mayorIigualFecha: mayorIigualFecha,
      });
      if (error) {
        this.$store.commit("alerta/addAlertas", { "estado": estados, "mensaje": mensaje, 'error': error })
      }
    },
    async inicializarClientes() {
      await this.$store.dispatch("MOD_clientes/obtenerClientes");
    },
    async inicializarEstados() {
      await this.$store.dispatch("MOD_ubicaciones/obtenerEstados");
    },
  },
  watch: {
    estado(event) {
      this.inicializarTrabajos({
        cliente: null,
        estado: event,
        menorIgualFecha: null,
        mayorIigualFecha: null,
      });
    },
    cliente(event) {
      this.inicializarTrabajos({
        cliente: event,
        estado: null,
        menorIgualFecha: null,
        mayorIigualFecha: null,
      });
    },
    tipo(evento){
      if(evento==='estado'){
        this.inicializarEstados()
      }
    }
  },
};
</script>
