<template>
  <v-row>
    <v-col cols="auto">
      <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="menorIgualFecha"
        transition="scale-transition"
        offset-y
        min-width="auto"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            v-model="menorIgualFecha"
            label="Fecha de inicio"
            prepend-icon="mdi-calendar"
            readonly
            v-bind="attrs"
            v-on="on"
            clearable
            outlined dense
          ></v-text-field>
        </template>
        <v-date-picker
          v-model="menorIgualFecha"
          no-title
          scrollable
          locale="es-es"
        >
        </v-date-picker>
      </v-menu>
    </v-col>
    <v-col cols="auto">
      <v-menu
        ref="menu2"
        v-model="menu2"
        :close-on-content-click="false"
        :return-value.sync="mayorIigualFecha"
        transition="scale-transition"
        offset-y
        min-width="auto"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            v-model="mayorIigualFecha"
            label="Fecha final"
            prepend-icon="mdi-calendar"
            readonly
            v-bind="attrs"
            v-on="on"
            clearable
            dense outlined
          ></v-text-field>
        </template>
        <v-date-picker
          v-model="mayorIigualFecha"
          no-title
          scrollable
          locale="es-es"
        >
        </v-date-picker>
      </v-menu>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data: () => ({
    menu: false,
    menu2: false,
    menorIgualFecha: new Date(
      Date.now() - new Date().getTimezoneOffset() * 60000
    )
      .toISOString()
      .substr(0, 10),
    mayorIigualFecha: new Date(
      Date.now() - new Date().getTimezoneOffset() * 60000
    )
      .toISOString()
      .substr(0, 10),
  }),
  watch: {
    menorIgualFecha(event) {
      const { mayorIigualFecha } = this;
      this.$emit("eventFecha", {
        cliente: null,
        estado: null,
        menorIgualFecha: event,
        mayorIigualFecha: mayorIigualFecha,
      });
      this.$refs.menu.save(event);
    },
    mayorIigualFecha(event) {
      const { menorIgualFecha } = this;
      this.$emit("eventFecha", {
        cliente: null,
        estado: null,
        menorIgualFecha: menorIgualFecha,
        mayorIigualFecha: event,
      });
      this.$refs.menu2.save(event);
    },
  },
};
</script>
