import axios from '@/api'

// import { localStorage2json, json2localStorage } from '@/utils/data/store';

// import { INITIALISER_FUNCTION, LOGOUT_FUNCTION } from '@/store/constants'

export default {
  namespaced: true,

  state: {
    respuesta: null,

    inmueble: {},
    inmuebles: [],
    inmueblesSeleccionados: [],
    cargando: false,

    flag_crear_inmuebles: 0,
    flag_obtener_inmuebles: 0,
    flag_eliminar_inmueble: 0,
  },

  mutations: {
    setInmueble(state, payload) { state.inmueble = payload },
    setInmuebles(state, payload) {
      // json2localStorage('inmuebles', payload)
      state.inmuebles = payload
    },
    addInmuebles(state, payload){
      state.inmuebles.push(payload)
    },
    limpiarInmuebles(state) {
      // json2localStorage('inmuebles', [])
      state.inmuebles = []
    },
    limpiarInmueble(state) {
      // json2localStorage('inmuebles', [])
      state.inmueble = {}
    },
    setCargando(state, payload) { state.cargando = payload },
    setInmueblesSeleccionados(state, payload) { state.inmueblesSeleccionados = payload },
    cleanInmueblesSeleccionados(state) { state. inmueblesSeleccionados = []},
    eliminarInmueble(state, payload) { state.inmuebles.splice(payload, 1) },
  },

  actions: {
    // [INITIALISER_FUNCTION]({ dispatch }) {
    //   dispatch('iniciarInmuebles');
    // },
    // [LOGOUT_FUNCTION]({ commit }) {
    //   commit('limpiarInmuebles')
    // },
    // iniciarInmuebles({ commit },) {
    //   const inmuebles = localStorage2json('inmuebles')
    //   commit('MUT_ListaInmuebles', inmuebles)
    // },
    async crearInmueble({commit}, { nombre, clave, direccion, estado, municipio, localidad, cliente }){
      try {
        commit('setCargando', true)
        const { data, status } = await axios.instance.post('inmuebles/inmueble/', {
          'name': nombre,
          'property_key': clave,
          'address': direccion,
          'province': estado.id,
          'municipality': municipio.id,
          'locality': localidad,
          'client': cliente
        })
        const { items, message } = data
        const { id, name, property_key, address, province, municipality, locality, client } = items
        const inmueble = {
          'id': id, 'nombre': name, 'clave': property_key, 'direccion': address, 'estado': province, 'municipio': municipality, 'localidad': locality, 'cliente': client
        }
        commit('addInmuebles', inmueble)
        commit('setCargando', false)
        return { 'mensaje': message, 'estado': status }
      } catch (error) {
        commit('setCargando', false)
        return { 'error': error }
      }
    },
    async verificarInmuebles({ commit }, inmuebles) {
      try {
        commit('setCargando', true)
        var inmuebles_verificar = []
        inmuebles.forEach(element => {
          inmuebles_verificar.push({
            'name': element.nombre,
            'property_key': element.clave,
            'address': element.direccion,
            'province': element.estado,
            'municipality': element.municipio,
            'locality': element.localidad,
            'client': element.cliente.id,
          })
        })
        const { data, status } = await axios.instance.post('inmuebles/inmueble/verificacion/', {'properties':inmuebles_verificar})
        const { properties } = data
        var inmuebles_verificados = []
        properties.forEach((element, index) => {
          if(element.error){
            inmuebles_verificados.push({
              'error': element.error[Object.keys(element.error)[0]],
              'status': element.status,
              'clave': inmuebles[index].clave,
              'nombre': inmuebles[index].nombre,
              'estado': { 'nombre': inmuebles[index].estado },
              'municipio': { 'nombre': inmuebles[index].municipio },
              'localidad': { 'nombre': inmuebles[index].localidad },
              'direccion': inmuebles[index].direccion,
              'cliente': inmuebles[index].cliente
            })
          } else {
            inmuebles_verificados.push({
              'clave': element.property_key,
              'nombre': element.name,
              'estado': { 'id': element.province.id, 'nombre': element.province.name },
              'municipio': { 'id': element.municipality.id, 'nombre': element.municipality.name },
              'localidad': element.locality ? { 'id': element.locality.id, 'nombre': element.locality.nombre } : {},
              'direccion': element.address,
              'cliente': { 'id': element.client.id, nombre: element.client.name}
            })
          }
        })
        commit('setInmuebles', inmuebles_verificados)
        commit('setCargando', false)
        return { 'mensaje': 'Consulta satisfactoria.', 'status': status }
      } catch (error) {
        commit('setCargando', false)
        return { 'mensaje': error.response.data.message, 'status': error.response.status }
      }
    },
    async AXN_CrearInmuebles({ state, commit }, inmuebles) {
      commit('MUT_Flag_CrearInmuebles', 1)
      try {
        var inmuebless = []
        inmuebles.forEach(inmueble => {
          inmuebless.push({
            name: inmueble.nombre_inmueble,
            property_key: inmueble.clave_inmueble,
            address: inmueble.direccion_inmueble,
            province_id: inmueble.id_estado,
            municipality_id: inmueble.id_municipio,
            locality_id: inmueble.id_localidad,
            client_id: inmueble.id_cliente,
          })
        })
        const { data } = await axios.instance.post('inmuebles/inmueble/confirmacion/',
          { 'properties': inmuebless }
        )
        const { items } = data
        commit('MUT_Respuesta', items)
        commit('MUT_Flag_CrearInmuebles', 2)
        return state.flag_crear_inmuebles
      } catch (error) {
        commit('MUT_Flag_CrearInmuebles', 3)
        return state.flag_obtener_inmuebles
      }
    },
    async recuperarInmueble({commit}, id_inmueble){
      try {
        commit('setCargando', true)
        const { data, status } = await axios.instance.get(`inmuebles/inmueble/${id_inmueble}`)
        const { items, message } = data
        const { id, name, property_key, address, province, municipality, locality, client } = items
        const inmueble = {
          'id': id,
          'clave': property_key,
          'nombre': name,
          'estado': province,
          'municipio': municipality,
          'localidad': locality,
          'direccion': address,
          'cliente': client
        }
        commit('setInmueble', inmueble)
        commit('setCargando', false)
        return { 'mensaje': message, 'status': status}
      } catch (error) {
        commit('setCargando', false)
        return { 'mensaje': error.response.data.error }
      }
    },
    async listarPorClientesTerritorio({ commit }, { cliente, estado, municipio, localidad }) {
      try {
        commit('setCargando', true)
        const { data, status } = await axios.instance.get(
          `inmuebles/inmueble/filtrado/?clients=${cliente}&province=${estado}&municipality=${municipio}&locality=${localidad}`
        )
        const { items } = data
        var inmuebles = []
        items.forEach(inmueble => {
          inmuebles.push({
            'id': inmueble.id,
            'clave': inmueble.property_key,
            'nombre': inmueble.name,
            'estado': inmueble.province,
            'municipio': inmueble.municipality,
            'localidad': inmueble.locality,
            'direccion': inmueble.address,
            'cliente': inmueble.client
          })
        })
        commit('setInmuebles', inmuebles)
        commit('setCargando', false)
        return { 'mensaje': 'Consulta satisfactoria.', 'estado': status }
      } catch (error) {
        commit('limpiarInmuebles')
        commit('setCargando', false)
        return { 'error': error }
      }
    },
    async eliminar({ state, commit }, id) {
      commit('MUT_Flag_EliminarInmueble', 1)
      try {
        const { data } = await axios.instance.delete(`inmuebles/inmueble/${id}/`,)
        commit('MUT_Respuesta', data)
        commit('MUT_Flag_EliminarInmueble', 2)
        return state.flag_eliminar_inmueble
      } catch (error) {
        commit('MUT_Flag_EliminarInmueble', 3)
        return state.flag_eliminar_inmueble
      }
    },
    async listar({commit}, cliente){
      try {
        commit('setCargando', true)
        const { data, status } = await axios.instance.get(cliente ? `/inmuebles/inmueble/?clients=${cliente}` : '/inmuebles/inmueble/')
        const { items, message } = data
        const inmuebles = []
        items.forEach(inmueble => {
          inmuebles.push({
            'id': inmueble.id,
            'clave': inmueble.property_key,
            'nombre': inmueble.name,
            'estado': inmueble.province,
            'municipio': inmueble.municipality,
            'localidad': inmueble.locality,
            'direccion': inmueble.address,
            'cliente': inmueble.client
          })
        })
        commit('setInmuebles', inmuebles)
        commit('setCargando', false)
        return { 'mensaje': message, 'estado': status }
      } catch (error) {
        commit('setCargando', false)
        return { 'error': error }
      }
    }
  },
  getters: {
    getInmuebles: (state) => state.inmuebles,
    getInmueble: (state) => state.inmueble,
    getCargando: (state) => state.cargando,
    getInmueblesSeleccionados: (state) => state.inmueblesSeleccionados
  }
}